import React, { useContext } from 'react'
import GitHubCalendar from 'react-github-calendar';
import styled from 'styled-components';
import { myTheme } from '../Context/Theme';
const GitCalender = () => {
    const {isAuth} = useContext(myTheme)
    const CalendarContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: ${isAuth?"#102c47":""};
`;
    const CalendarWrapper = styled.div`
  max-width: 1200px;
  width: 100%;
  padding: 20px;
  background-color: ${isAuth?"#102c47":""};

  @media (max-width: 480px) {
    padding: 10px;
  }
`;
  return (
    <div className="react-activity-calendar">
        <CalendarContainer>
        <CalendarWrapper >
             <GitHubCalendar username="saifkhan5417-2" blockSize={15} blockMargin={5} />
        </CalendarWrapper>
        </CalendarContainer >
      </div>
  )
}

export default GitCalender
