import React, { useContext } from 'react'
import { myTheme } from '../Context/Theme'

const Contact = () => {
    const {isAuth} = useContext(myTheme)
  return (
    <div class="container" style={isAuth?{backgroundColor: "#102c47" , color :"#ffc107"}:{}}>
     
        <form class="row g-3">
  <div class="col-md-6">
    <label for="inputEmail4" class="form-label">First Name</label>
    <input type="email" class="form-control" id="inputEmail4"/>
  </div>
  <div class="col-md-6">
    <label for="inputPassword4" class="form-label">Last Name</label>
    <input type="password" class="form-control" id="inputPassword4"/>
  </div>
  <div class="col-md-6">
    <label for="inputEmail4" class="form-label">Email</label>
    <input type="email" class="form-control" id="inputEmail4"/>
  </div>
  <div class="col-6">
    <label for="inputAddress" class="form-label">Address</label>
    <input type="text" class="form-control" id="inputAddress"/>
  </div>
  <div class="col-md-12">
    <div class="form-floating">
  <textarea class="form-control" placeholder="Leave a message here" id="floatingTextarea2" style={{height: "100px"}}></textarea>
  <label for="floatingTextarea2">Message</label>
</div>
  </div>
  <div class="col-12">
    <button type="submit" class="btn btn-warning">Send</button>
  </div>
</form>
<div className="row mt-5 g-4">
    <a href="https://github.com/saifkhan5417-2" className='nav-link col-md-3 col-sm-6' id="contact-github"><i class="fa-brands fa-github fa-bounce fa-2xl"></i></a>
    <a href="https://www.linkedin.com/in/saif-khan-84418522a/"  className='nav-link col-md-3 col-sm-6' id="contact-linkedin">
    <i class="fa-brands fa-linkedin fa-bounce fa-2xl" ></i>
          </a>
<p  id="contact-phone" className='col-md-3 col-sm-6'><i class="fa-solid fa-phone fa-shake fa-2xl"></i> 91+ 8795679740</p>
<p id="contact-email" className='col-md-3 col-sm-6'><i class="fa-solid fa-envelope fa-beat-fade fa-2xl"></i>  saifkhan5417.it@gmail.com</p>
</div>
</div>

  )
}

export default Contact
