import React, { useContext } from "react";
import { myTheme } from "../Context/Theme";

const Project = () => {
    const {isAuth} = useContext(myTheme)
  const ListItem = ({ children }) => (
    <li style={{ borderTop: "1px solid #ffc107", paddingTop: "0.5rem" }}>
      {children}
    </li>
  );
  return (
      <section id="projects" className="">
        <h1 className="mb-5 text-center">Projects</h1>
      <div className="container  row" style={{ margin: "auto" }}>
        <div className="col-lg-6 col-md-12 mb-4">
          <div className={`project-card ${isAuth?`text-bg-dark`:``} card`} style={{ maxWidth: "100%" }}>
            <img
              src={"https://wallpapercave.com/wp/wp8167063.jpg"}
              className="card-img-top"
              alt="Project Image"
              style={{ maxWidth: "100%" }}
            />
            <div className="card-body">
              <h3 className={`project-title ${isAuth?`text-warning`:``} card-title`}>
                Your Pharmacy
              </h3>
              <p className="project-description card-text">
                Discover pharmaceutical convenience with our HTML, CSS, and ES6
                powered Pharmacy Website. Seamlessly browse medications, manage
                prescriptions, and experience real-time updates for an
                unparalleled pharmaceutical journey. Your modern pharmacy
                awaits.
              </p>
              <div className="project-tech-stack">
                <h5>Tech stack:</h5>
                <ul>
                  <ListItem>HTML5</ListItem>
                  <li>CSS3</li>
                  <li>JavaScript</li>
                  <li>ES6 (ECMAScript 2015)</li>
                  <li>Responsive Design</li>
                  <li>Animations</li>
                  <li>JSON Server</li>
                  <li>Git</li>
                  <li>Netlify</li>
                </ul>
              </div>
              <div className="row ">
                    <a
                        href="https://github.com/pankajsbisht143/YourPharmacy"
                        className="project-github-link btn btn-warning  col-md-12 mb-4"
                    >
                        GitHub Repository
                    </a>
                    <a
                        href="https://64b13c3307923409cb6000fe--mellifluous-taffy-ec3e9b.netlify.app/"
                        className="project-deployed-link btn btn-warning  col-md-12 mb-4"
                    >
                        Deployed Project
                    </a>
              </div>
              
            </div>
          </div>
        </div>
        <div className="col-lg-6 col-md-12 mb-4">
          <div className={`project-card ${isAuth?`text-bg-dark`:``} card`} style={{ maxWidth: "100%" }}>
            <img
              src={"https://www.code-brew.com/wp-content/uploads/2018/05/SUCCESS-STORY-1.png"}
              className="card-img-top"
              alt="Project Image"
              style={{ maxWidth: "100%" }}
            />
            <div className="card-body">
              <h3 className={`project-title ${isAuth?`text-warning`:``} card-title`}>
                Big Basket Clone
              </h3>
              <p className="project-description card-text">
              Introducing "Big Basket Clone" - a revolutionary online grocery shopping platform, inspired by Big Basket. Embark on a seamless shopping journey with our user-friendly interface, extensive product range, and swift doorstep delivery. Enjoy the convenience of selecting fresh produce, pantry staples, and household essentials from the comfort of your home.
              </p>
              <div className="project-tech-stack">
                <h5>Tech stack:</h5>
                <ul>
                  <ListItem>HTML5</ListItem>
                  <li>CSS3</li>
                  <li>JavaScript</li>
                  <li>ES6 (ECMAScript 2015)</li>
                  <li>Responsive Design</li>
                  <li>Git</li>
                  <li>Netlify</li>
                </ul>
              </div>
              <div className="row ">
                    <a
                        href="https://github.com/punitsharma10/bigbasket/tree/cartPage?search=1"
                        className="project-github-link btn btn-warning  col-md-12 mb-4"
                    >
                        GitHub Repository
                    </a>
                    <a
                        href="https://647e005fed84ff2236edc1b9--fastidious-buttercream-e1aa8d.netlify.app"
                        className="project-deployed-link btn btn-warning  col-md-12 mb-4"
                    >
                        Deployed Project
                    </a>
              </div>
              
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Project;
