import React, { useContext } from 'react'
import { myTheme } from '../Context/Theme'

export default function Navbar({handleTheme , handleResumeButtonClick}) {
    const {isAuth} = useContext(myTheme)

    return (
        
        <>
            
{isAuth?<nav className="navbar bg-dark border-bottom border-warning fixed-top navbar-expand-lg bg-body-tertiary" data-bs-theme="dark" id="nav-menu">
  <div className="container-fluid">
    <a className="navbar-brand mx-5 text-warning" href="#">
      &lt;SAIF/&gt;
    </a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mx-3 mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active home" aria-current="page" href="#home">
            Home
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link about" href="#about">
            About
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link skills" href="#skills">
            Skills
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link projects" href="#projects">
            Projects
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link contact" href="#contact">
            Contact
          </a>
        </li>
        <li className="nav-item mt-2 mx-2" onClick={handleTheme}>
          {isAuth?<i className="fas fa-sun" style={{"color":"white" , "fontSize":"22px"}}></i>: <i class="fa-regular fa-moon" style={{"fontSize":"22px"}}></i>}
        </li>
      </ul>
      <button id="resume-link-1" className="btn btn-warning" onClick={handleResumeButtonClick}>
       
          Resume
      </button>
    </div>
  </div>
</nav>:<nav className="navbar fixed-top navbar-expand-lg bg-body-tertiary" id="nav-menu">
  <div className="container-fluid">
    <a className="navbar-brand mx-5 text-warning" href="#">
      &lt;SAIF/&gt;
    </a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mx-3 mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active home" aria-current="page" href="#home">
            Home
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link about" href="#about">
            About
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link skills" href="#skills">
            Skills
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link projects" href="#projects">
            Projects
          </a>
        </li>
        <li className="nav-item">
          <a className="nav-link contact" href="#contact">
            Contact
          </a>
        </li>
        <li className="nav-item mt-2 mx-2" onClick={handleTheme}>
        {isAuth?<i className="fas fa-sun" style={{"color":"white" , "fontSize":"22px"}}></i>: <i class="fa-regular fa-moon" style={{"fontSize":"22px"}}></i>}
        </li>
      </ul>
      <button id="resume-link-1" className="btn btn-warning" onClick={handleResumeButtonClick}>
          Resume
      </button>
    </div>
  </div>
</nav>}
        </>
    )
}
